.app {
  width: 100%;
  max-width: 1920px;
  min-width: 320px;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  background-color: #565a75;
  padding: 1rem auto;
  box-sizing: border-box;
  position: relative;
}

.container {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabletContainer {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}

.wrapper{
  width: 100%;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem auto;
}
.first {
  margin-top: 0;
}
.del {
  height: 1400px;
}

