.about {
	width: 100%;
	height: 100%;
	display: flex;
	position: relative;
	background-color: #565a75;
}
.about__imgContainer {
	width: 50%;
	height: 100%;
	position: relative;
    z-index: 1;
    background: #fff url(./aboutUs.jpg) center center no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
}
.about__textContainer {
    width: 60%;
    height: 70%;
    color: rgba(81, 64, 121, 1);
	position: absolute;
	right: 4%;
	top: 20%;
	z-index: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 1);
	box-shadow: 0.9rem 1rem 0.4rem rgba(0, 0, 0, 0.5);
}
.about__textContainer h1 {
	width: 100%;
	height: 10%;
    text-align: center;
    font-size: 2rem;
	text-shadow: 0 0 0.63rem #e55b7e;
	background-color: rgba(255, 255, 255, 0.9);
	margin: 0;
}
.about__textContainer p {
    width: 100%;
    text-align: start;
    font-size: 1.4rem;
	padding: 0.2rem 2rem;
	text-shadow: -0.08rem 0 0 #000;
}

@media(max-width: 900px){
    .about__imgContainer {
		width: 60%;
	}
}
@media(max-width: 800px){
	.about__imgContainer,
	.about__textContainer {
		width: 70%;
	}
}
@media(max-width: 700px){
	.about__imgContainer,
	.about__textContainer {
		width: 80%;
	}
}
@media(max-width: 600px){
	.about__imgContainer,
	.about__textContainer {
		width: 90%;
	}
}
@media(max-width: 500px){
	.about__imgContainer,
	.about__textContainer {
		width: 100%;
	}
	.about__textContainer {
		right: 0;
		box-shadow: 0 1rem 0.4rem rgba(0, 0, 0, 0.5);
	}
}