.home {
    width: 100%;
    height: 100%;
    position: relative;
    background: #000 url(./home.jpg)  no-repeat;
    margin: 0;
    padding: 0;
    
}
.cover {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
}

.flex-center {
    display: flex;
    justify-content: center;
	align-items: center;
	
}

.home-titel-wave {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
	width: 60%;
	height: 27rem;
    overflow: hidden; 
    background-color: rgba(0, 0, 0, 0.8);
   /*  margin-top: 30%;
    margin-left: 3%; */
    
}

.home-titel-wave h1 {
    font-family: 'Popping', sans-serif;
    margin: 2.6rem auto;
	font-size: 10rem;
	display: block;
	position: absolute;
	top: 0;
	text-align: center;
	letter-spacing: 0.63rem;
	color: rgba(255, 255, 255, 1);
	text-shadow: 0 0 0.63rem #e55b7e;
}



.home-titel-wave h1:nth-child(2){
	color: rgba(205, 182, 188, 1);
	animation: animWater 4s ease-in-out infinite;
}

@keyframes animWater {
	0%,100% {
		clip-path: polygon(0% 44%, 11% 50%, 23% 57%, 37% 62%, 49% 60%, 66% 45%, 74% 41%, 81% 41%, 90% 48%, 100% 59%, 100% 100%, 0 100%);
	}
	50% {
		clip-path: polygon(0 71%, 12% 71%, 20% 62%, 31% 53%, 40% 49%, 51% 50%, 61% 56%, 72% 67%, 85% 71%, 100% 73%, 100% 100%, 0 100%);
	}
	
}

.home-social {
    width: 100%;
    height: 7rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 15rem;
    z-index: 3;
}

.home-social svg {
    width: 5rem;
    height: 5rem;
    fill: #fff;
	background-color: #9f62aa; 
	border: 0.2rem solid rgba(255, 255, 255, 0.5);
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
    cursor: pointer;
    transition: all linear;
}

.home-social svg:hover {
    width: 4rem;
	height: 4rem;
    transform: translateX(-0.2rem) translateY(-0.2rem);
    box-shadow: 0 0 0.5rem rgb(255, 255, 255, 0.7);
}

@media(max-width: 1500px){
    .home-titel-wave {
        width: 80%;
    }
}
@media(max-width: 800px){
    .home {
        background-color: #565a75;
        background-image: none;
    }
    .home-titel-wave {
        background-color: transparent;  
    }
}
@media(max-width: 560px){
    .home-titel-wave h1 {
        margin: 1.6rem auto;
        font-size: 8rem;
    }
}
@media(max-width: 450px){
    .home-titel-wave h1 {
        font-size: 7rem;
    }
}
@media(max-width: 450px){
    .home-titel-wave h1 {
        font-size: 7rem;
    }
}
@media(max-width: 350px){
    .home-titel-wave h1 {
        font-size: 6rem;
    }
}