.loadingSpinner {
    $colors: #4b888b, #6495ac, #426a8b, #1d4786, #092155f5;
    display: flex;
    justify-content: center;
    align-items: center;
    animation-delay: 1s;
    margin: 0 auto;

    .dotFromSpinner {
        position: relative;
        width: 2em;
        height: 2em;
        margin: 0.8em;
        border-radius: 50%;

        &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: inherit;
        border-radius: inherit;
        animation: wave 2s ease-out infinite;
        }

        @for $i from 1 through 5 {
            &:nth-child(#{$i}) {
                background: nth($colors, $i);

                &::before {
                animation-delay: $i * 0.2s;
                }
            }
        }
    }
}

@keyframes wave {
50%,
    75% {
        transform: scale(2.5);
    }

80%,
    100% {
        opacity: 0;
    }
}
  