.delivery__list-content{
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
    list-style: none;
    overflow-y: scroll;
    padding: 0;
    margin: 0;
    box-sizing: border-box; 
}
.delivery__list-content li {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0.3rem 0;
    background-color: rgba(0, 0, 0, 0.5);
}
.del-list-remove {
    width: 2rem;
    height: 2rem;
    background-color: rgba(255, 255, 255, 0.5);
    border: 0.15rem solid #fff;
    border-radius: 0.3rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1rem;
}
.del-list-remove svg {
    width: 1.8rem;
    height: 1.8rem;
    fill: rgb(180, 28, 28);
}
.delivery__list-content li p{
    font-size: 1.3rem;
}
.totalItem {
    margin-right: 1rem;
}