.contactUs {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	background: #565a75 url("./contact2.png") center top no-repeat;
	-webkit-background-size: cover;
 	-moz-background-size: cover;
 	-o-background-size: cover;
	background-size: cover; 
	overflow-y: scroll;
}
.contactUs__info {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	
	box-sizing: border-box;
	
}
.contactUs__info-header-tab {
	width: 5%;
	height: 90%;
	writing-mode: vertical-lr;
	text-align: center;
	letter-spacing: 0.5rem;
	background-color: #be9ddf;
	color: rgb(255, 255, 255);
	font-size: 2.5rem;
	border: 0.1rem solid rebeccapurple;
	border-radius: 0.7rem;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
}

.contactUs__tabcontent {
	display: none;
	position: absolute;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.7);
	transition: all;
}
.show-content {
	display: flex;
	width: 80%;
	height: 90%;
	position: relative;
	
}

/*====================================================================
				SECTION "Map"
====================================================================*/
.map {
	margin: 0 auto;
	width: 80%;
	height: 90%;
	background-color: rgba( 0, 0, 0, 0.5);
	border: 0.3rem solid #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 0 0 1rem #000;
}
.map iframe {
	width: 100%;
	height: 100%;
	
}
.resylt-form {
	width: 100%;
    height: 100%;
    color: #000;
    font-size: 3rem;
	background-color: #fff;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	padding: 0 1rem;
	word-wrap: break-word;
}
.resylt-form p {
	font-size: 2.5rem;
}

@media(max-width: 1800px) {
	.contactUs__info-header-tab {
		font-size: 2.5rem;
	}
}
@media(max-width: 1670px) {
	.contactUs__info-header-tab {
		font-size: 2rem;
	}
}
@media(max-width: 1300px) {
	.contactUs__info-header-tab {
		font-size: 1.5rem;
	}
}
@media(max-width: 1000px) {
	.contactUs__info-header-tab,
	.show-content {
		height: 85%;
		margin-top: 5%;
	}
}
@media(max-width: 600px) {
	.contactUs__info {
		flex-direction: column;
		padding-top: 15%;
	}
	.contactUs__info-header-tab {
		width: 90%;
		height: 7%;
		writing-mode: horizontal-tb;
		margin-top: 0;
	}
	
	.contactUs__tabcontent {
		display: none;
		position: absolute;
		justify-content: center;
		align-items: center;
		transition: all;
	}
	.show-content {
		display: flex;
		width: 80%;
		height: 90%;
		position: relative;
		
	}
}

@media(max-width: 450px) {
	.contactUs__info {
		padding-top: 20%;
	}
}