.navbarBurger__container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 10%;
	background-color: rgba(0, 0, 0, 0.4);
	z-index: 10000;
}
.containerHeight {
	height: 100%;
}
.burger:focus {
	outline: none !important;
}

.burger {
	width: 12%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	cursor: pointer;
	background-color: transparent;
	border: none;
	z-index: 1;
	margin: auto 0;
}

.burgerIcon {
	width: 80%;
	height: 0.5rem;
	background-color: #fff;
	border-radius: 1.25rem;
	position: relative;
}

.burgerIcon::before,
.burgerIcon::after {
	width: 100%;
	height: 0.5rem;
	content: '';
	display: block;
	background-color: #fff;
	border-radius: 1.25rem;
	padding-top: -1.25rem;
	position: absolute;
}

.burgerIcon::before {
	top: -1.25rem;
}

.burgerIcon::after {
	bottom: -1.25rem;
}
.burgerIcon:active,
.burgerIcon:focus {
    outline: none !important;
}

.burger__active .burgerIcon {
	background-color: transparent;
	transition: 0.5s transform inline;
}

.burger__active .burgerIcon::before {
	top: 0;
	transform: rotate(45deg);
	
}

.burger__active .burgerIcon::after {
	bottom: 0;
	transform: rotate(-45deg);
}



.burgerNav {
	width: 100%;
	height: 90vh;
	display: none;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	background:linear-gradient(to top, #9198e5, #ba83c4 60%, #ba83c4 60%);
	transition: all 0.5s;
}
.burger__active + .burgerNav {
	display: flex;
	animation: animBurgerOpenMenu 2s ease-in-out;
	
}
@keyframes animBurgerOpenMenu {
	0% {
		opacity: 0;
		height: 0;
	}
	100% {
		height: 80vh;
		opacity: 1;
	}
}
.burger__active-hide + .burgerNav {
	display: flex;
	animation: animBurgerCloseMenu 2s ease-in-out;
}
@keyframes animBurgerCloseMenu {
	0% {
		width: 100%;
		opacity: 1;
	}
	100% {
		width: 0;
		opacity: 0;
		display: none;
	}
}
.burgerNav a {
	text-decoration: none;
	font-size: 2rem;
	color: #fff;
}
.burgerNav a:hover {
	text-decoration: underline;
	text-shadow: -0.2rem -0.1rem 0 #000;
	background-color: #9395a5;
}