.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.delivery {
    width: 100%;
    height: 100%;
    display: flex;
    box-sizing: border-box;
    position: relative;
    background-color: #565a75;
}
.container .del-width {
    flex-direction: row;
}
.tabletContainer .del-width {
    flex-direction: column;
    padding: 0 1rem;
}

.tabs-list__wrapper,
.delivery-list__wrapper {
    display: flex;
    flex-direction: column;
    /* background-color: rgb(187, 151, 255); */
    background-color: #565a75;
    border: 0.3rem solid #fff;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    position: relative;
}
.container .delivery  .cl-w {
    width: 50%;
    height: 100%;
}
.tabletContainer .delivery .cl-w {
    width: 100%;
    height: 50%;
}
.delivery__item-list {
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    position: relative;
    border-top: 0.3rem solid #fff;
}
.delivery__item-list h2,
.delivery-list__wrapper h2 {
    width: 100%;
    height: auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(86, 3, 119);
    color: #fff;
    margin: 0;
}

.delivery-list__wrapper {
    color: #fff;
    font-size: 1.5rem;
    justify-content: space-between;
}

.delivery-list__wrapper h2 {
    height: 10%; 
}

.delivery-submit {
    width: 100%;
    height: 40%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    font-size: 1.5rem;
    color: #fff;
    background-color: rgba(86, 3, 119, 0.582);
}
.delivery-list__total-price {
    width: 100%;
    text-align: end;
    padding-right: 2rem;
    font-size: 2rem;
}
.delivery-list__submit-phone {
    width: 80%;
    height: 2.5rem;
    border: 0.2rem solid red;
    border-radius: 0.2rem;
}
.submit-order {
    width: 80%;
    height: auto;
    margin: 0.5rem auto;
    border: 0.2rem solid #fff;
    border-radius: 1rem;
    background-color: #b31a80;
    color: #fff;
    cursor: pointer;
    font-size: 2rem;
    font-weight: 700;
}

.submit-order:hover {
    background-color: #702256fd;
}
.submitMessage {
    width: 90%;
    height: 50%;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    background-color: #702256fd;
    border: 0.15rem solid #fff;
    display: block;
    color: #fff;
    font-size: 5rem;
    text-align: center;
    z-index: 100;

}
.result_onSubmit {
    width: 100%;
    height: auto;
    color: #000;
    font-size: 1rem;
    background-color: #fff;
    border: 0.2rem solid red;
    word-wrap: break-word;
}
.result_onSubmit  h3 {
    font-size: 1.5rem;
    color: #000;
}
