/*=================================================================== 
							SECTION "SOCIAL"
 =================================================================*/
.contactSocial {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.social {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	align-items: center;
}
.contact__sociaIconlContainer {
	width: 30%;
	height: 100%;
	border: 0.32rem solid #fff;
	border-radius: 2rem;	
	display: flex;
	justify-content: center;
	align-items: center;
}
.contact__sociaIconlContainer:hover {
	background-color: #be9ddf;
	transform: translate(-0.5rem, -0.5rem);
	box-shadow: 0.9rem 1rem 0.4rem rgba(0, 0, 0, 0.5);
}
.contact__sociaIconlContainer a {
	width: 7rem;
	height: 7rem;
	background-color: #565a75; 
	border: 0.13rem solid rgba(255, 255, 255, 0.5);
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	cursor: pointer;
}

.contact__sociaIconlContainer a svg{
	display: block;
	width: 7rem;
	height: 7rem;
	fill: #fff;
}

@media(max-width: 580px){
	.contact__sociaIconlContainer {
		width: 50%;
		margin: 2rem;

	}
}
