.nav {
    width: 20%;
    height: 100%;
    display: flex; 
    flex-direction: column;
    justify-content: space-evenly;
    border-right: 0.25rem solid orchid;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    border: 0.3rem solid #fff;
    border-radius: 50%;
    background-color: rgba(81, 64, 121, 1);
    margin: 0.5rem auto;
    overflow: hidden;
    z-index: 1;
}
.logo svg {
    width: 3.7rem;
    height: 3.7rem;
    display: block;
	fill: rgba(255, 255, 255, 1);
    z-index: 2;
}

.nav-item {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: start;
    padding-left: 1rem;
    list-style: none;
}

.nav-item li {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2rem;
    padding: 1rem;
}
.nav-item li:hover {
    text-shadow: -0.2rem 0.2rem 0.1rem #000;
    border-bottom: 0.2rem solid #fff;
}

.nav-item li a {
    color: #fff;
    text-decoration: none;
}

@media (max-width: 1600px) {
    .logo {
        width: 5rem;
        height: 5rem;
    }
    .logo svg {
        width: 4rem;
        height: 4rem;
    }
    .nav-item li {
        font-size: 1.5rem;
    }
}
@media (max-width: 1250px) {
    .nav-item li {
        font-size: 1.5rem;
    }
}
@media (max-width: 1000px) {
    .nav {
        display: none;
    }
}