/*=================================================================== 
							SECTION "ADRESS"
 =================================================================*/
 .contactAdressList {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
 }
 .contactAdressList ul {
    margin: 0;
    padding-left: 0.5rem;
    display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	
 }

.contactAdressList ul li{
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	margin: 0.63rem auto;
	padding: 0.63rem;
	justify-content: flex-start;
	align-items: center;
    font-size: 3rem;
    color: #fff;
}
.contactAdressList ul li svg {
	width: 5rem;
	height: 5rem;
	fill: #fff;
}
.a__contact {
	text-decoration: none;
	cursor: pointer;
    color: #fff;
    padding-left: 0.5rem;
}

.a__contact:hover {
	text-decoration: underline;
	cursor: pointer;
}

@media(max-width: 1300px) {
	.contactAdressList ul li{
		font-size: 2rem;
	}
}
@media(max-width: 450px) {
	.contactAdressList ul li{
		font-size: 1.5rem;
	}
	.contactAdressList ul li svg {
		width: 3.8rem;
		height: 3.8rem;
	}
}