.team-container {
    background:linear-gradient(to top, #9198e5, #ba83c4 60%, #ba83c4 60%);
}
.team {
    width: 80%;
    margin: 1rem auto ;
    
}

.team__card-container{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.team__card {
    width: 500px;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    border: 0.4rem solid #fff;
	background-color: #fff;
    box-shadow: 0 0 2rem #aaa;
}

.team__card-img {
    width: 100%;
    height: 400px;
    position: relative;
    background-color: transparent;
    background-origin: content-box;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 2;
}

.team__social {
    width: 12rem;
    height: 10rem;
    position: absolute;
    bottom: -5rem;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: transparent;
    z-index: 3;
}


.team__social svg  {
    width: 5rem;
	height: 5rem;
	background-color: #9f62aa; 
    border: 0.5rem solid rgba(255, 255, 255, 1);
    fill: #fff;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
    cursor: pointer;
    transition: all linear;
}

.team__card h2,
.team__card h3,
.team__card p {
    color: #000000;
    width: 90%;
    height: auto;
    text-align: start;
    margin: 0.3rem 0;
}

.team__card h2 {
    font-size: 3rem;
    padding-left: 3rem;
}

.team__card h3 {
    font-size: 2rem;
    padding-left: 2rem;
}
.team__card p {
    display: block;
    font-size: 1.3rem;
    font-style: italic;
    padding-left: 0.5rem;
}

.cover {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
}

