.flex-center {
    display: flex;
	justify-content: center;
	align-items: center;
}

.menu__mainContainer {
	width: 94%;
	height: 90vh;
    display: flex;
    flex-direction: column;
	margin: 0.5rem auto;
}
.menu__navigationContainer {
	width: 100%;
	height: 10vh;
	
}
.menu__navigationContainer ul {
	width: 100%;
	height: 100%;
	list-style: none;
	padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.menu__navigationContainer ul li{
    width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
    background-color: #faf;
    border: 0.5rem solid rgba( 255, 255, 255, 0.5);
	border-top-left-radius: 2rem;
	border-top-right-radius: 2rem;
	overflow: hidden;
	transition: 0.1ms transform linear;
	
}

.menu__navigationContainer ul li.active {
	border: 0.5rem solid rgba( 255, 255, 255, 1);
	transform: translate(0, 10%);
	background-color: #fff;
}

.menu__icon {
	width: 100%;
	min-width: 45px;
	height: 100%;
    background-color: #fff;
}

.menu__icon svg {
	display: block;
	width: 3rem;
	height: 3rem;
	margin: 0;
	fill: #740c59;
}
.active .menu__icon svg {
	fill: transparent;
	stroke-width: 10;
	stroke: #6e0c49;
	stroke-dasharray: 3000;
	stroke-dashoffset: 0;
	animation: draw 4s ease;
}

@keyframes draw {
	0% {
		stroke-dashoffset: 3000;
	}
	100% {
		stroke-dashoffset: 0;
	}
}

@keyframes puls {
	0%{
		box-shadow: -0.2rem 0 0 #000000,
					0 0 0.06rem #ba83c4, 
					0 0 0.3rem #fca3be;
	}
	100%{
		box-shadow: -0.2rem 0 0 #000000,
					0 0 0.13rem #ba83c4, 
					0 0 0.63rem #fca3be, 
					0 0 1.56rem #fca3be, 
					0 0 3.13rem #fca3be;
	}
}


.menu__Container {
	width: 100%;
    height: 90%;
    position: relative;
	background-color: #fff;
    box-sizing: border-box;
}
.menu__buttonContainer {
	width: 20%;
	min-width: 130px;
	height: 15%;
	position: absolute;
	top: 0.5rem;
	left: 0.5rem;
	background-color: rgba( 255, 255, 255, 0.5);
	display: flex;
	flex-wrap: wrap;
	z-index: 10;
}
.menu__buttonContainer button {
	width: 50%;
	height: 50%;
	margin: 0;
	padding: 0;
}

.menu__buttonContainer button svg {
    display: block;
	width: 4.5rem;
	height: 2rem;
	margin: 0;
	fill: #740c59;
}
.menu__slideCounter {
	width: 100%;
}
.menu__slideCounter p {
	width: 100%;
	height: 100%;
	font-size: 2rem;
	color: #000;
	margin: 0;
	padding: 0;
	text-align: center;
}
.menuCard__item {
	width: 100%;
    height: 100%;
    overflow: hidden;
}

.menuCard__itemList {
    width: 100%;
    height: 100%;
    position: relative;
	background-color: #fff;
    border: 0.5rem solid rgba( 255, 255, 255, 1);
	display: flex;
}

.menuCard__photoIntro {
	width: 65%;
	height: 100%;
    display: flex;
    flex-wrap: wrap;
}
.menuCard__photo {
	width: 100%;
	height: 70%;
    overflow: hidden;
    background-color: transparent;
    background-origin: content-box;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
}


.menuCard__name {
	width: 100%;
	color: #000;
	font-size: 3rem;
	text-align: start;
	padding-left: 1rem;
}
.menuCard__price {
	width: 100%;
	font-size: 2.4rem;
	color: #000;
	font-weight: 600;
	font-family: 'Open Sans', sans-serif;
    text-align: start;
    padding-left: 0.5rem;
}
.menuCard__description {
    width: 50%;
    height: 50%;
    position: absolute;
    right: 3%;
    top: 50%;
    transform: translate(0, -50%);
    background-color: rgba( 0, 0, 0, 0.7);
}
.menuCard__description p {
	width: 100%;
	text-align: center;
    color: rgba( 255, 255, 255, 1);
	font-size: 2rem;
	padding: 0 1rem;
}

@media(max-width: 1300px) {
	.menuCard__description p {
		font-size: 1.5rem;
	}
}
@media(max-width: 1000px) {
	.menu__mainContainer {
		width: 100%;
		margin: 0;
		padding-top: 5%;
	}
}
@media(max-width: 950px) {
	.menuCard__photoIntro {
		width: 70%;
	}
	.menuCard__description {
		width: 60%;
	}
}
@media(max-width: 800px) {
	.menu__mainContainer {
		flex-direction: row;
	}
	.menu__navigationContainer {
		width: 10vw;
		height: 100%;
		margin-left: 1rem;
	}
	.menu__navigationContainer ul {
		flex-direction: column;
		justify-content: space-evenly;
	}
	
	.menu__navigationContainer ul li{
		height: 11.11%;
		display: block;
		border-top-left-radius: 2rem;
		border-top-right-radius: 0;
		border-bottom-left-radius: 2rem;
	}
	
	.menu__navigationContainer ul li.active {
		transform: translate(10%, 0);
	}
	.menu__Container {
		width: 90vw;
		height: 100%;
	}	
	.menuCard__photoIntro {
		width: 100%;
	}
	.menuCard__description {
		width: 100%;
		right: 0;
	}
}

@media(max-width: 550px) {
	.menu__mainContainer {
		padding-top: 10%;
	}
	.menu__navigationContainer {
		width: 15vw;
	}
	.menu__Container {
		width: 85vw;
	}
}
@media(max-width: 450px) {
	.menuCard__name {
		font-size: 2rem;
	}
	.menuCard__price {
		font-size: 1.7rem;
	}
}
@media(max-width: 380px) {
	.menu__mainContainer {
		padding-top: 15%;
	}
	.menu__navigationContainer {
		width: 20vw;
	}
	.menu__Container {
		width: 80vw;
	}
}