.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.delivery__item-list-content {
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
    list-style: none;
    overflow-y: scroll;
    padding: 0;
    margin: 0;
    box-sizing: border-box; 
    
}

.delivery__item-list-content li {
    width: 100%;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.15rem solid #000;
    background-color: rgba(0, 0, 0, 0.5);
}
.delivery__item-list-content li p {
    width: 25%;
    font-size: 1.1rem;
    padding: 0.5rem 0 0.5rem 2%;
}

.del-list-info {
    height: 2.5rem;
    width: 2.5rem;
}
.del-list-info svg{
    width: 1.7rem;
    height: 1.7rem;
    fill: #fff;
}
.del-select-size {
    width: 16%;
    font-size: 1rem;
}
.del-quantity-items {
    width: 16%;
    height: 70%;
    font-size: 1.2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.del-quantity-items button {
    width: 35%;
    height: 100%;
    font-size: 2rem;
    
}
.del-item-total {
    width: 10%;
    font-size: 1.5rem;
}
.del-item-add {
    width: 12%;
    height: 80%;
    font-size: 1.2rem;
    margin-right: 0.5rem;
}

@media(max-width: 1220px) {
	.delivery__item-list-content li p {
        font-size: 0.9rem;
    }
    .del-select-size {
        font-size: 0.8rem;
    }
    .del-quantity-items button {
        font-size: 1.5rem;
    }
    .del-quantity-items {
        font-size: 1rem;
    }
    .del-item-total {
        font-size: 1.2rem;
    }
    .del-item-add {
        font-size: 1rem;
    }
} 