.wrapper-happy-client {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}
.arrow {
    width: 8%;
    height: 50%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(250, 20, 20, 0.5);
    color: #fff;
    font-size: 2rem;
    font-weight: 700;
    writing-mode: vertical-lr;
    text-align: center;
    cursor: pointer;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
}
.prev-arrow {
    left: 0;
   /*  transform: rotate(180deg), translateY(-50%); */
}
.next-arrow {
    right: 0;
}
.happy-client-dots-wrapper {
    position: absolute;
    bottom: 5%;
    width: 100%;
    z-index: 100;
}
.happy-client-dots-wrapper ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    width: 70%;
    height: 100%;
    margin: 0 auto;
}
.happy-client-dots-wrapper ul li {
    width: 10%;
    height: 0.5rem;
    background-color: rgba(255, 255, 255, 0.5);
    border: 0.15rem solid #fff;
    border-radius: 0.5rem;
    
}
.happy-client-dots-wrapper ul li.active {
    background-color: rgba(255, 255, 255, 1);
}
.container-happy-client {
    margin: auto;
    padding: 0;
    box-sizing: border-box;
    width: 90%;
    height: 88%;
    display: flex;
    justify-content: space-around;
}
.card {
    position: relative;
    margin: auto;
    width: 70%;
    height: 70%;
    background-color: #fff;
    transform-style: preserve-3d;
    transform: perspective(3500px);
    transition: 1s;
    box-shadow: inset 30px 0 50px rgba(0, 0, 0, 0.5);
    display: none;
}
.show {
    display: block;
}

.openCard {
    position: absolute;
    width: 40px;
    height: 100%;
    top: 0;
    right: -40px;
    padding: auto 0;
    background-color: red;
    font-size: 2rem;
    font-weight: 700;
    writing-mode: vertical-lr;
    letter-spacing: 0.3rem;
    text-align: center;
    color: #fff;
}
.imgBox {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0.15rem solid #be9ddf;
    
    transform-origin: left;
    transition: 1s;
    z-index: 50;
    overflow: hidden;
    box-sizing: border-box;
    background-color: transparent;
    background-origin: content-box;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
 	-moz-background-size: cover;
 	-o-background-size: cover;
	background-size: cover; 
}
.card:hover .imgBox {
   transform: perspective(3500px) rotateY(-135deg); 
}
.details {
    width: 100%;
    height: 100%;
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
.details h2 {
    font-size: 2.1rem;
}
.details p {
    font-size: 1.7rem;
}
.img1 {
    background-image: url('./happyClient-img/1.jpg');
}
.img2 {
    background-image: url('./happyClient-img/2.jpg');
}
.img3 {
    background-image: url('./happyClient-img/3.jpg');
}
.img4 {
    background-image: url('./happyClient-img/4.jpg');
}
.img5 {
    background-image: url('./happyClient-img/5.jpg');
}

@media(max-width: 1600px) {
    .details h2 {
        font-size: 1.9rem;
    }
    .details p {
        font-size: 1.4rem;
    }
}
@media(max-width: 450px) {
    .details h2 {
        font-size: 1.4rem;
    }
    .details p {
        font-size: 1.2rem;
    }
    .container-happy-client {
        margin: 0 auto;
        height: 90%;
    }
    .card {
        margin: auto;
        width: 90%;
        height: 80%;
    }
    .openCard {
        width: 100%;
        height: 40px;
        bottom: -40px;
        top: 100%;
        right: 0;
        writing-mode: horizontal-tb;
    }
}