.help {
    width: 70%;
    height: 70%;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    font-size: 1rem;
    background-color: #fff;
    color: #000;
    border: 0.3rem solid #000;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    opacity: 0;
    cursor: pointer;
    transition: 1s;
    z-index: 100;
    display: none;
    padding: 0.3rem;
}
.show-help {
    opacity: 1;
    display: block;
}
.helpButton {
    width: 12%;
    height: 12%;
    margin: 1rem;
    font-size: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    background-color: rgba(150, 0, 5, 1);
    
}
