/*====================================================================
				SECTION "FORM"
====================================================================*/

.contactForm__container {
	width: 100%;
	height: 100%;
	position: relative;
	font-family: "Roboto", Helvetica, Arial, sans-serif;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
}

#contact input[type="text"],
#contact input[type="email"],
#contact textarea,
#contact button[type="submit"] {
  	font: 400 2rem/2.5rem "Roboto", Helvetica, Arial, sans-serif;
}

#contact {
	width: 90%;
	height: 100%;
  	background: rgba(86, 90, 117, 0.8);
  	padding: 1.6rem;
  	box-shadow: 0 0 1.25rem 0 rgba(86, 90, 117, 0.2), 0 0.3rem 0.3rem 0 rgba(86, 90, 117, 0.3);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
}

#contact h3 {
	display: block;
	font-size: 2.5rem;
	font-weight: 400;
	margin-bottom: 0.63rem;
}

#contact h4 {
	margin: 0.3rem 0 0.94rem;
	display: block;
	font-size: 2rem;
	font-weight: 400;
}

fieldset {
	border: medium none !important;
	margin: 0 0 0.63rem;
	min-width: 100%;
	padding: 0;
	width: 100%;
}

#contact input[type="text"],
#contact input[type="email"],
#contact textarea {
	width: 100%;
	border-radius: 0.3rem;
	border: 0.13rem solid #ccc;
	background: #FFF;
	margin: 0 0 0.3rem;
	padding: 0.63rem;
	/* font-size: 2rem; */
}

#contact input[type="text"]:hover,
#contact input[type="email"]:hover,
#contact textarea:hover {
	-webkit-transition: border-color 0.3s ease-in-out;
	-moz-transition: border-color 0.3s ease-in-out;
	transition: border-color 0.3s ease-in-out;
	border: 0.13rem solid #aaa;
}

#contact textarea {
	height: 100%;
	max-width: 100%;
	resize: none; /* vertical */
}

#contact button[type="submit"] {
	cursor: pointer;
	width: 100%;
	border: none;
	background: #ebcfb9;
	color: #ac5089;
	margin: 0 0 0.3rem;
	padding: 0.63rem;
	border-radius: 0.3rem;
}

#contact button[type="submit"]:hover {
	background: #bb439b;
	color: #fff;
	-webkit-transition: background 0.3s ease-in-out;
	-moz-transition: background 0.3s ease-in-out;
	transition: background-color 0.3s ease-in-out;
}

#contact button[type="submit"]:active {
 	box-shadow: inset 0 0.06rem 0.19rem rgba(0, 0, 0, 0.5);
}

#contact input:focus,
#contact textarea:focus {
	outline: 0;
	border: 0.06rem solid #aaa;
}

::-webkit-input-placeholder {
  	color: #888;
}

:-moz-placeholder {
  	color: #888;
}

::-moz-placeholder {
 	color: #888;
}

:-ms-input-placeholder {
  	color: #888;
}

@media(max-width: 450px) {
	.contactAdressList ul li{
		font-size: 1.5rem;
	}
	#contact input[type="text"],
	#contact input[type="email"],
	#contact textarea,
	#contact button[type="submit"] {
		font-size: 1.7rem;
	}

	#contact h3 {
		font-size: 2rem;
	}

	#contact h4 {
		font-size: 1.5rem;
	}
}