.center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.text {
    font-size: 3rem;
    color: #fff;
    text-shadow: -0.1rem 0.1rem 0 #2a4158, 
				-0.2rem 0.2rem 0.5rem #71879a, 
				-0.25rem 0.25rem 1rem  #b1c5d4,
                -0.3rem 0.3rem 2rem #d6d7dc;
}

.offers-container {
    width: 95%;
    height: 95%;
    margin: auto;
    padding: 0;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(3, 33.33%);
    grid-template-rows: repeat(3, 33.33%);
}
.offers-card {
    position: relative;
    border: 0.15rem solid #fff;
    overflow: hidden;
}
.card1 {
    grid-column: 1/3;
    grid-row: 1/2;
}
.card2 {
    grid-column: 3/4;
    grid-row: 1/3;
}
.card3 {
    grid-column: 2/3;
    grid-row: 2/3;
}
.card4 {
    grid-column: 1/2;
    grid-row: 2/4;
}
.card5 {
    grid-column: 2/4;
    grid-row: 3/4;
}


.front,
.back {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: 1s linear;
}
.front {
    z-index: 2;
}
.hidden {
    opacity: 0;
}
.back {
    background-color: #fff;
    z-index: 1;
}
.back h2,
.back p {
    padding: 0.3rem 0.7rem;
    font-size: 1.5rem;
}
.picture {
    width: 100%;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    background-color: transparent;
    background-origin: content-box;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
 	-moz-background-size: cover;
 	-o-background-size: cover;
	background-size: cover; 
}
.picture1 {
    background-image: url('./img/1.jpg');
}
.picture2 {
    background-image: url('./img/2.jpg');
}
.picture3 {
    background-image: url('./img/3.jpg');
}
.picture4 {
    background-image: url('./img/4.jpg');
}

@media(max-width: 1250px) {
    .text {
        font-size: 2.5rem;
    }
}
@media(max-width: 1000px) {
    .offers-container {
        height: 90%;
        grid-template-rows: repeat(3, 30%);
        margin-top: 10%;
    }
}
@media(max-width: 650px) {
    .offers-container {
        width: 100%;  
        grid-template-columns: 100%;
        grid-template-rows: repeat(20, 15%);
    }
    .card1 {
        grid-column: 1/2;
        grid-row: 2/5;
    }
    .card2 {
        grid-column: 1/2;
        grid-row: 5/11;
    }
    .card3 {
        border: none;
        grid-column: 1/2;
        grid-row: 1/2;
    }
    .card4 {
        grid-column: 1/2;
        grid-row: 11/17;
    }
    .card5 {
        grid-column: 1/2;
        grid-row: 17/20;
    }
}
@media(max-width: 400px) {
    .offers-container {
        margin-top: 15%;
    }
}