.del-tabs {
    width: 100%;
    height: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.del-tabs h2 {
    width: 100%;
    height: auto;
    font-size: 2.7rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: rgb(86, 3, 119);
    margin: 0;
    padding: 0;
}

.del-tabs__icon {
    width: 7rem;
    height: 7rem;
    margin: 0.5rem 1rem;
    border: 0.2rem solid #b09fc2;
    border-radius: 1rem;
    background-color: #ffF;
    transition: linear;
}
.del-tabs__icon:hover {
    transform: translate(-0.3rem, -0.3rem);
}
.del-tabs__icon svg {
    display: block;
	width: 4.8rem;
	height: 4.8rem;
    margin: 0;
    fill: #4e2475;
}
@media(max-width: 1820px) {
	.del-tabs__icon {
        width: 6rem;
        height: 6rem;
    }
    .del-tabs__icon svg {
        width: 4.4rem;
        height: 4.4rem;
    }
}

@media(max-width: 1620px) {
	.del-tabs__icon {
        width: 5rem;
        height: 5rem;
    }
    .del-tabs__icon svg {
        width: 4rem;
        height: 4rem;
    }
}
@media(max-width: 750px) {
	
    .del-tabs__icon {
        width: 4rem;
        height: 4rem;
    }
    .del-tabs__icon svg {
        width: 3rem;
        height: 3rem;
    }
}
@media(max-width: 700px) {
    .del-tabs {
        height: 12rem;
    }
    .del-tabs__icon {
        margin: 1rem auto;
    }
}

@media(max-width: 400px) {
	
    .del-tabs__icon {
        width: 3.5rem;
        height: 3.5rem;
    }
    .del-tabs__icon svg {
        width: 2.5rem;
        height: 2.5rem;
    }
}
@media(max-width: 350px) {
    .del-tabs__icon {
        width: 3rem;
        height: 3rem;
    }
    .del-tabs__icon svg {
        width: 2rem;
        height: 2rem;
    }
}